.characters {
    font-family: 'Architects Daughter', cursive;
    background: url("https://wallpaperaccess.com/full/983279.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    text-align: center;
}

.characters-image {
    background: url("https://img6.gram.pl/jnwy20200415113437188kfnx.jpg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    height: 50vh;
    text-align: start;
    position: relative;
    overflow: hidden;
    padding: 1em;
}

.characters h1 {
    padding-top: 2em;
    margin: 0;
    padding-left: 5%;
    text-shadow: 0 0.1em 0.1em black;
}

.characters input {
    width: 20%;
    font: inherit;
    border: 1px solid #ccc;
    background: #f8f8f8;
    padding: 0.15rem 0.25rem;
    margin-top: 2em;
    border-radius: 5px;
}

.characters input:focus {
  outline: none;
  background: #ebebeb;
  border-color: #510077;
}

.characters .characters-list {
    display: flex;
    padding: 0 2em;
    justify-content: space-evenly;
    flex-wrap: wrap;
    min-height: 20vh;
}

.characters a {
    margin: 3em;
    text-align: center;
    color: #b39809;
    text-decoration: none;
}

.characters .character-card {
    height: 30vh;
    transition: 1s;
}

.characters .character-card:hover {
    transform: scale(1.1);
}

.characters .character-card img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    border-radius: 5px;
    box-shadow: 0 0 0.2em;
}

.characters .character-card p {
    width: 100%;
    height: 20%;
}
